*, *::after, *::before {
    box-sizing: border-box;
}

body {
    background-color: #efefef;
}

.admin-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    align-items: center;
    gap: 20px;
    /* background-color: #fff; */
}

.breacrumb-section {
    width: 100%;
    height: 100px;
}

.user-item-list {
    width: min(1200px, 80%);
    background-color: #fff;
    overflow: auto;
    height:600px;
}

.user-item-list thead {
    position: sticky;
    top:0;
}

.search-bar {
    width: 100%;
    display: flex;
    justify-content: center;
}

.search-bar input{
    border-radius: 15px;
    height: 30px;
    width: 500px;
    padding: 0 15px;
}

.user-item-list table {
    width: 100%;
    text-align: center; 
    vertical-align: middle;
}

.user-item-list table td {
    width: 75px;
    height: 30px;
}

.user-item-list table td, .user-item-list table th{
    background-color: #fff;
}

.user-item-list table th{
    font-size: 24px;
}

.user-info:hover td{ 
    cursor: pointer;
    background-color: #efefef;
}
